/* Dashboard Layout */
.dashboard-layout {
    display: flex;
    flex-direction: column;
    height: 100vh;
    background-color: #f9f9f9;
}

/* Navbar */
.navbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 60px;
    /* Set a consistent height */
    background: #ffffff;
    padding: 1rem 1.5rem;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
    z-index: 200;
    /* Always above the sidebar */
    display: flex;
    justify-content: space-between;
    align-items: center;
}

/* Content Area */
.dashboard-content {
    flex-grow: 1;
    margin-left: 250px;
    /* Leave space for the sidebar */
    margin-top: 60px;
    /* Leave space for the navbar */
    padding: 2rem;
    background: white;
    overflow-y: auto;
}

/* Responsive Adjustments */
@media (max-width: 768px) {
    .dashboard-content {
        margin-left: 0;
        /* Full width when sidebar is hidden */
    }
}