/* Clock-In Button */
.clockin-button {
    display: inline-block;
    /* Ensure it's displayed as a button */
    margin-top: auto;
    /* Push it to the bottom of the container if needed */
    padding: 12px 20px;
    /* Adjust padding for size */
    background-color: #1e88e5;
    /* Bright blue */
    color: #ffffff;
    /* White text */
    border: none;
    border-radius: 6px;
    /* Smooth rounded corners */
    cursor: pointer;
    /* Pointer cursor on hover */
    font-size: 16px;
    /* Readable font size */
    font-weight: bold;
    text-align: center;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    /* Subtle shadow for depth */
    transition: background-color 0.3s ease, transform 0.2s ease;
    /* Smooth hover effects */
    width: 100%;
    /* Full-width button */
}

.clockin-button:hover {
    background-color: #1565c0;
    /* Darker blue on hover */
    transform: translateY(-2px);
    /* Lift effect */
}

.clockin-button:active {
    transform: translateY(0);
    /* Reset lift effect on click */
}

.clockin-button.disabled {
    background-color: #cfd8dc;
    /* Neutral gray for disabled */
    color: #607d8b;
    /* Subtle dark gray for text */
    cursor: not-allowed;
    /* Prevent interaction */
    box-shadow: none;
    /* Remove shadow for disabled state */
}